










































































































































@import '../assets/scss/variables';

.logs__header {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin: 0 auto 30px;
  width: 100%;
  > * {
    margin: 0 20px 20px 0;
  }
  > h1 {
    text-align: center;
    font-size: 1.2rem;
  }
  > .at-input {
    flex-grow: 1;
  }
  > .at-select {
    max-width: 150px;
    &:last-child {
      margin-right: 0;
    }
  }
}

.logs__items {
  display: flex;
  flex-wrap: wrap;
}

.log {
  display: flex;
  flex-direction: column;
  margin: 10px;
  padding: 10px;
  box-shadow: $box-shadow1;
  transition: .3s;
  .log__name {
    font-weight: bold;
    text-align: center;
    margin-bottom: 15px;
  }
  .log__footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: .8rem;
    opacity: .8;
    > span {
      padding-left: 15px;
    }
  }
}

#pagination {
  text-align: center;
  font-size: 0;
  padding: 20px 0;
  margin-top: 30px;
  > .at-pagination {
    display: inline-block;
  }
}
